.button {
    width: 100px;
    height: 30px;
    border: none;
    outline: none;
    color: #fff;
    background: #eba300;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border-radius: 20px;
    font-weight: bold;
    font-family: 'Raleway', sans-serif;
}

.button:before {
    content: '';
    background: linear-gradient(
            45deg,
            #ff0000,
            #ff7300,
            #fffb00,
            #48ff00,
            #00ffd5,
            #002bff,
            #7a00ff,
            #ff00c8,
            #ff0000
    );
    position: absolute;
    top: -2px;
    left: -2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
    border-radius: 20px;
}

.button:active {
    color: #000;
}

.button:active:after {
    background: transparent;
}

.button:hover:before {
    opacity: 1;
}

.button:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #eba300;
    left: 0;
    top: 0;
    border-radius: 20px;
}

@keyframes glowing {
    0% {
        background-position: 0 0;
    }
    50% {
        background-position: 400% 0;
    }
    100% {
        background-position: 0 0;
    }
}

.button:disabled {
    cursor: not-allowed;
    opacity: 0.5;
    background: #727070;
}

.button:disabled:after {
    background: #afa986;
}

.red {
    background: #991b27;
}

.red:after {
    background: #991b27;
}
